.burbujas div {
  filter: blur();
  background: #fff1f1;
  border: 1px solid #fff1f1;
  border-radius: 100%;
  animation: animarburbujas 4s linear infinite;
  position: absolute;
  bottom: -200px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, .1);
}

.burbujas div:nth-child(1) {
  width: 300px;
  height: 300px;
  animation-duration: 4s;
  left: 40px;
}

.burbujas div:nth-child(2) {
  width: 60px;
  height: 55px;
  animation-duration: 8s;
  left: 25%;
}

.burbujas div:nth-child(3) {
  width: 30px;
  height: 27px;
  animation-duration: 5s;
  left: 40%;
}

.burbujas div:nth-child(4) {
  width: 65px;
  height: 60px;
  animation-duration: 10s;
  left: 50%;
}

.burbujas div:nth-child(5) {
  width: 55px;
  height: 50px;
  animation-duration: 6.5s;
  left: 65%;
}

.burbujas div:nth-child(6) {
  width: 50px;
  height: 48px;
  animation-duration: 9s;
  left: 85%;
}

.burbujas div:nth-child(7) {
  width: 35px;
  height: 32px;
  animation-duration: 6s;
  left: 90%;
}

@keyframes animarburbujas {
  0% {
    width: 0;
    height: 0;
  }

  3% {
    width: 30px;
    height: 27px;
  }

  50% {
    opacity: .5;
    margin-left: 20px;
  }

  100% {
    background: #fff1f1;
    bottom: 100vh;
    transform: rotate(90deg);
  }
}

@keyframes cambiar {
  0% {
    background-position: 0;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0;
  }
}

